
.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: darkkhaki;
    height: 100vh;
    width: 100%;
}
.functionBox {
    width: 25%;
    height: 100%;
    background-color: beige;
}
.showData {
    width: 75%;
    height: 100%;
    background-color: antiquewhite;
}
.controlBox {
    width: 100%;
    height: 100px;
    background-color: orange;
}
.functionBtns li{
    box-sizing: border-box;
    width: 100%;
    height: 60px;
    /* background-color: red; */
    text-align: center;
    line-height: 60px;
    padding: 5px;
    cursor: pointer;
    transition: all .3s;
}
.functionBtns li:hover {
    background-color: rgb(242, 226, 226);
}
.img {
    display: block;
    width: 100px;
    height: 100px;
    background-color: red;
}
.aply {
    display: flex;
    align-items:center;
    justify-content: center;

    width: 100%;
    height: 100%;
}
.controlBox h2 {
    text-align: center;
    line-height: 100px ;
}
.black {
    width: 100%;
    height: 100px;
    background-color: rgb(4,21,39);
}