.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    border-radius: 8px;
    overflow: hidden;
}
.SporterInfo {
    width: 50%;
    height: 100%;
    background-color: #7cbddb;
}
.SporterInfoAction {
    width: 50%;
    height: 100%;
    background-color: azure;
}
.showInfo {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 1.2em;
    height: 30px;
    padding: 5px 20px;
}
.Button {
    margin: 30px 0 20px 20px;
}