.ModalBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 360px;
    height: 180px;
    border: 1px solid #eee;
    border-radius: 5px;
    background-color: white;
}

.comfirmText {
    text-align: left;
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 1em;
    padding-left: 30px;
    border-bottom: 1px #eee solid;
}
.describeText {
    text-align: left;
    padding-left: 30px;
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 0.5em;
    border-bottom: 1px #eee solid;
}
.BtnBox {
    display: flex;
    justify-content:right;
    align-items: center;
    width: 100%;    
    height: 60px;
    padding: 0 10px;
    background-color: #fff;
}
.BtnBox button {
    width: 72px;
    height: 36px;
    outline: none;  
    border: 0;
    border-radius: 5px;
    margin-right: 20px;
    transition: all .3s;
}
.affirm {
    background-color: rgb(0, 94, 255);
    color: #fff;
    cursor: pointer;
}
.affirm:hover {
    background-color: rgb(23, 80, 171);
}
.cancle:hover {
    background-color: rgb(211, 207, 207);
}
.Zindex {
    z-index: 999;
}