
.RegisterBox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background: linear-gradient(200deg,#f3e7e9,#e3eeff);
}

.RegisterBox .formStyle {
    height: 50%;
    width: 25%;
    background-color: #d3b7d8;
    padding: 10%;
    border-radius: 2%;
}
.formStyle h2{
    text-align: center;
}
.Box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.inputStyle {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.inputStyle input {
    background-color: transparent;
    width: 80%;
    color: #fff;
    border: none;
    /* 下边框样式 */
    border-bottom: 1px solid rgba(255,255,255,0.4);
    padding: 10px 0;
    text-indent: 10px;
    margin: 8px 0;
    font-size: 14px;
    letter-spacing: 2px;
    
}
.inputStyle span {
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputStyle input::placeholder{
    color: #fff;
}
.inputStyle input:focus{
    color: #a262ad;
    outline: none;
    border-bottom: 1px solid #a262ad80;
    transition: 0.5s;
}
.inputStyle input:focus::placeholder{
    opacity: 0;
}
.loginBtn:hover{
    background-color: #a262ad;
    color: #f6f6f6;
    transition: background-color 0.5s ease;
} 
.btnBox a:hover{
    background-color: #a262ad;
    color: #f6f6f6;
    transition: background-color 0.5s ease;
} 

.btnBox {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.btnBox a,
.loginBtn  {
    font-size: 14px;
    margin-top: 3%;
    background-color: #fff;
    color: #a262ad;
    border: 1px solid #d3b7d8;
    padding: 6px 10px;
    border-radius: 5px;
    letter-spacing: 1px;
    outline: none;
    cursor: pointer;
    transition: all;
}
.idChoose {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.bgColor {
    background: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%) no-repeat;
    width: 100%;
    height: 100%;
}