.RainbowBox {
    display: flex;
    width: 100%;
    height: 8px;
    background-color: #ffc8cb;  
}
.RainbowBox div {
    position: relative;
    width: 25%;
    font-size: 0.5em;
    overflow: hidden;
}
.RainbowBox div:nth-child(1){
    background-color: #088bd4;
}
.RainbowBox div:nth-child(1)::after{
    content: '1';
    display: inline-block;
    position: absolute;
    right: 0px;
    top: -12px;
    width: 10px;
    height: 50px;
    background-color: #0bbe63;
    transform: rotate(-135deg);
    z-index: 2;
}
