.head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 40px;
    background-color: aquamarine;
}
.headPicBox {
    display: flex;
    justify-content: space-between;
    width: 160px;
    height: 40px;
}
.headPicBox span{
    display: block;
    /* width: 160px; */
    font-size: 0.5em;
    text-align: center;
    line-height: 40px;
    cursor:pointer;
}
.headPicBox img {
    display: block;
    width: 40px;
    border-radius: 50%;
    padding-right: 10px;
    
}
.headPicBox div{
    font-size: 1em;
    height: 100%;
    display: flex;
    align-items: center;
    padding-right: 8px;
}