:global(.ant-layout-header){
    margin-bottom: 0;
    background-color: aqua;
    padding: 0;
}
.liBox {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin:10px 0 10px 0;
    border-bottom:2px solid #eee;
    padding-bottom: 10px;
} 
.liBox li {
    padding: 5px;
    box-sizing: border-box;
    transition: all .4s;
    border-radius: 5px;
    cursor: pointer;
}
.liBox li:hover {
    color: #1677ff;
}
.imgbox img{
    width: 100%;

}
.siderBox {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    width: 100%;
}
.siderBox div {
    width: 100%;
    height: 50%;
    /* background-color: red;   */
    padding: 8px;
}
.siderBox div img {
    width: 100%;
}
.siderBox div span{
    font-size: 12px;
    color: rgb(160, 155, 155);
}
.freeBlock {
    display: flex;
    align-items: stretch;
    width: 100%;
    background-color: red;
    
}
.freeBlock div {
    width: 100%;
    background: #000;
}
.textBox {
    -webkit-box-reflect: below 1px linear-gradlent(transparent,rgba(0,0,0,0,2))
}
.textBox h2 {
    color: #fff;
    font-size: 1em;
    letter-spacing: 15px;
    text-transform: uppercase;
    text-align: center;
    outline: none;
    --c:lightseagreen;
    text-shadow: 0  0 10px var(--c),
    0 0 20px var(--c),
    0 0 40px var(--c),
    0 0 60px var(--c),
    0 0 80px var(--c);
    animation: animate 5s linear infinite;
}
@keyframes animate{
    to{
        filter: hue-rotate(360deg);
    }
}