.navbox {
    /* width: 100%;  */
    height: 100%;    
    font-size: 1em;
}
.libox {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    height: 100%;
}
.libox li {
    padding-left: 10px;
    padding-right: 10px;
    height: 100%;
}
.libox li:nth-child(4) {
    margin-right: 20px;
}
.libox li a {
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    transition: all .3s;
}
.libox li a:hover{
    color: #1677ff;
}
